import { profileAPI } from "../api/api";

let initialState = {
  message: [
    { id: 1, mess: "IT IS ME", like: 5 },
    { id: 2, mess: "IT IS YOUR BAG", like: 9 },
  ],
  profile: null,
  status: '',
};
const profilePageReducer = (state = initialState, action) => {
  if (action.type === "ADD-POST") {
    let addMessage = {
      id: 3,
      mess: action.text,
      like: 0,
    };
    return {
      ...state,
      message: [...state.message, addMessage],
    };
  } else if (action.type === "DELETE_MESSAGE") {
    return {
      ...state,
      message: state.message.filter((u) => {
        return u.id != action.id;
      }),
    };
  } else if (action.type === "SET_PROFILE") {
    return {
      ...state,
      profile: action.profile,
    };
  } else if (action.type === "SET_STATUS") {
    return {
      ...state,
      status: action.status,
    };
  } else if (action.type === "SET_PHOTO") {
    return {
      ...state,
      profile: {...state.profile, photos: action.photoFile }
    };
  }
  return state;
};

export const addPost = (text) => {
  return {
    type: "ADD-POST",
    text,
  };
};
export const deleteMessage = (id) => {
  return {
    type: "DELETE_MESSAGE",
    id,
  };
};

export const setProfile = (profile) => {
  return {
    type: "SET_PROFILE",
    profile,
  };
};
export const setStatus = (status) => {
  return {
    type: "SET_STATUS",
    status,
  };
};
export const setPhoto = (photoFile) => {
  return {
    type: "SET_PHOTO",
    photoFile,
  };
};

export const getProfileThunk = (userId) => {
  return async (dispatch) => {
    let response = await profileAPI.getProfile(userId);
    dispatch(setProfile(response.data));
  };
};
export const getStatusThunk = (userId) => {
  return async (dispatch) => {
    let response = await profileAPI.getStatus(userId);
    dispatch(setStatus(response.data));
  };
};
export const updateStatusThunk = (status) => {
   return async (dispatch) => {
    let response = await profileAPI.updateStatus(status);
    if(response.data.resultCode === 0){
      dispatch(setStatus(status))
    }
  };
};
export const savePhotoThunk = (photoFile) => {
   return async (dispatch) => {
    let response = await profileAPI.savePhoto(photoFile);
    if(response.data.resultCode === 0){
      dispatch(setPhoto(response.data.data.photos))
    }
  };
};
export default profilePageReducer;
