import { authAPI } from "../api/api";
import { stopSubmit } from "redux-form";

let initialState = {
    id: null,
    login: null,
    email: null,
    isAuth: false
  };
  const authReducer = (state = initialState, action) => {
    if (action.type === "SET_USERS_AUTH") {
      return {
         ...state,
         ...action.data,
         };
    } 
    return state;
  };
  
  export const setUsersAuth = (id, login, email,isAuth) => {
    return {
      type: "SET_USERS_AUTH",
      data: {id, login, email,isAuth},
    };
  };

  export const getIsAuthThunk = () => {
    return async(dispatch) => {
       let response = await authAPI.getIsAuth()
        if (response.data.resultCode === 0 ){
          let {id,email,login} = response.data.data
          dispatch(setUsersAuth(id,email,login,true))
      }
    }
  }

  export const loginThunk = (email,password,rememberMe= false) => {
    return dispatch => {
      authAPI.login(email,password,rememberMe).then(response =>{
        if (response.data.resultCode === 0 ){
          dispatch(getIsAuthThunk())
        }else{
          let message = response.data.messages.length > 0 ? response.data.messages[0] : "Some error"
          dispatch(stopSubmit("login", {_error: message}))
        }
      })
    }
  }
  export const logoutThunk = () => {
    return dispatch => {
      authAPI.logout().then(response =>{
        if (response.data.resultCode === 0 ){
          dispatch(setUsersAuth(null,null,null,false))
          console.log("complited")
        }
      })
    }
  }
  
  
  export default authReducer;