import { userAPI, followAPI } from "../api/api";

let initialState = {
  users: [],
  pageSize: 10,
  portionCount: 5,
  totalCount: 0,
  pageCount: 0,
  currentPage: 1,
  isFetching: true,
  disableProcessState: false,
  disableID: null
};
let userFollowUnfollow = (state,action,bool) => {
  return {
    ...state,
    users: state.users.map(u => {
      if (u.id === action.userId) {
        return {
          ...u,
          followed: bool
        };
      }
      return u;
    })
  };
}
const UsersReducer = (state = initialState, action) => {
  if (action.type === "FOLLOW") {
    userFollowUnfollow(state,action,true)
   
  } else if (action.type === "UNFOLLOW") {
    userFollowUnfollow(state,action,false)
  } else if (action.type === "SET_USERS") {
    return {
      ...state,
      users: [...action.users]
    };
  } else if (action.type === "CHANGE_CURRENT_PAGE") {
    return {
      ...state,
      currentPage: action.id,
      disableID: action.id
    };
  } else if (action.type === "SET_TOTAL_COUNT") {
    return {
      ...state,
      totalCount: action.totalCount
    };
  } else if (action.type === "IS_FETCHING") {
    return {
      ...state,
      isFetching: action.bool
    };
  } else if (action.type === "DISABLE_PROCESS") {
    return {
      ...state,
      disableProcessState: action.bool
    };
  }
  return state;
};

export const follow = userId => {
  return {
    type: "FOLLOW",
    userId
  };
};
export const unfollow = userId => {
  return {
    type: "UNFOLLOW",
    userId
  };
};
export const setUsers = users => {
  return {
    type: "SET_USERS",
    users
  };
};
export const changeCurrentPage = id => {
  return {
    type: "CHANGE_CURRENT_PAGE",
    id,
    disableID: id
  };
};
export const setTotalCount = totalCount => {
  return {
    type: "SET_TOTAL_COUNT",
    totalCount
  };
};
export const isFetchingToggle = bool => {
  return {
    type: "IS_FETCHING",
    bool
  };
};
export const disableProcess = bool => {
  return {
    type: "DISABLE_PROCESS",
    bool
  };
};

export const getUsersThunk = (currentPage, pageSize) => {
  return dispatch => {
    dispatch(isFetchingToggle(true));
    userAPI.getUsers(currentPage, pageSize).then(response => {
      dispatch(setUsers(response.data.items));
      dispatch(setTotalCount(response.data.totalCount));
      dispatch(isFetchingToggle(false));
    });
  };
};

export const getChangedUsersThunk = (e,pageSize) => {
  return dispatch => {
    dispatch(changeCurrentPage(e));
    dispatch(isFetchingToggle(true));
    dispatch(disableProcess(true));
    userAPI.getUsers(e, pageSize).then(response => {
      dispatch(setUsers(response.data.items));
      dispatch(isFetchingToggle(false));
      dispatch(disableProcess(false));
    });
  };
};

export const followThunk = (userId) => {
  return async dispatch => {
    let response = await followAPI.handleUnfollow(userId)
      if(response.data.resultCode === 0){
        dispatch(unfollow(userId))
      }
  }
}
export const unFollowThunk = (userId) => {
  return async dispatch => {
    let response = await followAPI.handleFollow(userId)
      if(response.data.resultCode === 0){
        dispatch(follow(userId))
      }
  }
}

export default UsersReducer;

