import { createStore, combineReducers, applyMiddleware, compose } from "redux";
import dialogPageReducer from './dialog-Reducer'
import navPageReducer from './nav-Reducer'
import usersReducer from "./users-Reducer";
import authReducer from "./auth-Reducer";
import thunkMiddle from "redux-thunk"
import { reducer as formReducer } from 'redux-form'
import appReducer from "./app-Reducer";
import profilePageReducer from "./profile-Reducer";
let reducers = combineReducers({
    profilePage: profilePageReducer,
    dialogPage: dialogPageReducer,
    navPage: navPageReducer,
    usersPage: usersReducer,
    auth: authReducer,
    form: formReducer,
    app: appReducer
})
const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
let store = createStore(reducers, composeEnhancers(applyMiddleware(thunkMiddle)))
window.store = store

export default store