import { getIsAuthThunk } from "./auth-Reducer";

let initialState = {
    initialized: false
  };
  const appReducer = (state = initialState, action) => {
    if (action.type === "TOGGLE_INITIALIZE") {
      return {
         ...state,
         initialized: true
         };
    } 
    return state;
  }; 
  export const initialToggle = () => {
    return {
      type: "TOGGLE_INITIALIZE",
    };
  };
 
  export const initialThunk = () => {
    return dispatch => {
     let promise = dispatch(getIsAuthThunk())
     Promise.all([promise]).then( () =>{
        dispatch(initialToggle())
     })
    }
  }

  export default appReducer;