import React from "react";
import "./App.css";
import Nav from "./components/Nav/nav";
import { Route, withRouter } from "react-router-dom";
import HeaderContainer from "./components/Header/headerContainer";
import { connect } from "react-redux";
import Preloader from "./components/common/loader/preloader";
import { initialThunk } from "./redux/app-Reducer";
import { compose } from "redux";
import { withSuspense } from "./components/Hoc/withSuspense";

const DialogContainer = React.lazy(() =>
  import("./components/Dialog/dialogContainer")
);
const Fake = React.lazy(() =>
  import("./components/common/new")
);
const ProfileContainer = React.lazy(() =>
  import("./components/Profile/profileContainer")
);
const UsersContainer = React.lazy(() =>
  import("./components/Users/usersContainer")
);
const Login = React.lazy(() =>
  import("./components/Login/login")
);

class App extends React.Component {
  componentDidMount() {
    this.props.initialThunk();
  }

  render() {
    if (!this.props.initialized) {
      return <Preloader />;
    }
    return (
      <div className="app-wrapper">
        <HeaderContainer />
        <Nav />
        <div className="app-wrapper-content">
          <Route
            path="/profile/:userId?"
            render={withSuspense(ProfileContainer)}
          />
          <Route
            path="/message"
            render={withSuspense(DialogContainer)}
          />
          <Route path="/users" render={withSuspense(UsersContainer)} />
          <Route path="/login" render={withSuspense(Login)} />
          <Route path="/news" render={withSuspense(Fake)} />
        </div>
      </div>
    );
  }
}

let mapToStateProps = (state) => {
  return {
    initialized: state.app.initialized,
  };
};

export default compose(
  connect(mapToStateProps, { initialThunk }),
  withRouter
)(App);
