let initialState = {
  messFrom: [
    { id: 1, name: "Sarvar" },
    { id: 2, name: "Rustam" },
    { id: 3, name: "Dilshod" },
    { id: 4, name: "Akram" },
    { id: 5, name: "Salohiddin" },
    { id: 6, name: "Javohir" }
  ],
  messContent: [
    { id: 1, content: "Mening Yurtim" },
    { id: 2, content: "Yangiliklar to'plami" },
    { id: 3, content: "Yurtimda qish" }
  ]
};
const dialogPageReducer = (state = initialState, action) => {
  if (action.type === "ADD-MESSAGE") {
    let mess = {
      id: 4,
      content: action.text
    };
    return {
       ...state,
       messContent: [...state.messContent, mess],
       };
  }
  return state;
};

export const addMessageActionCreator = (text) => {
  return {
    type: "ADD-MESSAGE",
    text
  };
};

export default dialogPageReducer;
